<template><markdown-wrapper><h1 id="timepicker">TimePicker <a class="header-anchor" href="#timepicker" aria-hidden="true">&#128279;</a></h1>
<blockquote>
<p>A lightweight &amp; configurable time picker.</p>
</blockquote>
<p><ins class="adsbygoogle"
     style="display:block; text-align:center;"
     data-ad-layout="in-article"
     data-ad-format="fluid"
     data-ad-client="ca-pub-4714899946256166"
     data-ad-slot="4603582855"></ins></p>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<p>To change the time input, you can:</p>
<ul>
<li>Click on the add / minus button.</li>
<li>Use up / down key of keyboard.</li>
<li>Use mouse wheel.</li>
<li>Input directly.</li>
</ul>
<p><strong>Note</strong>: Make sure to update the <code v-pre>v-model</code> reference when trying to change it from outside the component.</p>
<p>e.g. <code v-pre>time = new Date(time)</code></p>
<time-picker-example ref="time-picker-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">time-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"time"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">time</span>: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>()
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- time-picker-example.vue --&gt;</span>
</code></pre>
