<h2 id="range-limit">Range limit <a class="header-anchor" href="#range-limit" aria-hidden="true">&#128279;</a></h2>
<p>Example that limit time range from <strong>8:00 AM</strong> to <strong>8:00 PM</strong>:</p>
<time-picker-limit-example ref="time-picker-limit-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">time-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"time"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"max"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"min"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">time</span>: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(),
        <span class="hljs-attr">min</span>: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(<span class="hljs-string">'2017/01/01 8:00'</span>), <span class="hljs-comment">// date doesn't matter</span>
        max: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(<span class="hljs-string">'2017/01/01 20:00'</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- time-picker-limit-example.vue --&gt;</span>
</code></pre>
<h2 id="readonly">Readonly <a class="header-anchor" href="#readonly" aria-hidden="true">&#128279;</a></h2>
