<h2 id="timepicker-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/timepicker/TimePicker.vue">TimePicker</a> <a class="header-anchor" href="#timepicker-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Date</td>
<td></td>
<td>âœ”</td>
<td>The selected time.</td>
</tr>
<tr>
<td><code v-pre>show-meridian</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Use 12H or 24H mode.</td>
</tr>
<tr>
<td><code v-pre>hour-step</code></td>
<td>Number</td>
<td>1</td>
<td></td>
<td>Hours to increase or decrease when using a button.</td>
</tr>
<tr>
<td><code v-pre>min-step</code></td>
<td>Number</td>
<td>1</td>
<td></td>
<td>Minutes to increase or decrease when using a button.</td>
</tr>
<tr>
<td><code v-pre>readonly</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td></td>
</tr>
<tr>
<td><code v-pre>max</code></td>
<td>Date</td>
<td></td>
<td></td>
<td>The maximum time that user can select or input.</td>
</tr>
<tr>
<td><code v-pre>min</code></td>
<td>Date</td>
<td></td>
<td></td>
<td>The minimum time that user can select or input.</td>
</tr>
<tr>
<td><code v-pre>icon-control-up</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-up</td>
<td></td>
<td>The arrow icon shown inside the <code v-pre>increase</code> button.</td>
</tr>
<tr>
<td><code v-pre>icon-control-down</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-down</td>
<td></td>
<td>The arrow icon shown inside the <code v-pre>decrease</code> button.</td>
</tr>
<tr>
<td><code v-pre>controls</code></td>
<td>Boolean</td>
